import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup} from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component';
import { AmChartsService, AmChart } from "@amcharts/amcharts3-angular";
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
 
@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./customer-landing.component.html", 
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION
  ],
})
export class CustomerLandingComponent implements OnInit,AfterViewInit {
	@ViewChild(ListComponent) list: ListComponent;
	@ViewChild(ToastComponent) toast: ToastComponent;
	 
	listHide: boolean = true;
	addHide: boolean = false;
	animateAdd: string = 'out';
	animateList: string = 'in';
	configCustomerName = ''; 
	customerLandingForm: FormGroup; 
	bootstrapFormValues = [];
 	bootstrapMsg='';
 	grandTotal:string;
 	customerstartdatemsg:string;
    customerenddatemsg:string; 
	customerCloseSummaryHeader = [];
	customerCloseSummaryData = [];
	customerSummaryData = [];
	customerSummaryHeader = [];
	msg=[]; 
	branchs=[];
	customerSummary = []; 
	private chart: AmChart;
	pageIndex: number=1;
	customer = new customerData();

	  constructor(private _script: ScriptLoaderService, 	private http: HttpClient,private AmCharts: AmChartsService) {
		this.configCustomerName = sessionStorage.getItem('configCustomerName');
    }

	   
    ngOnInit() {
		this.listHide = true;
		this.animateAdd='out';
    	this.animateList='in';
		 this.getTodaysDate();
		this.customerLandingForm = new FormGroup({	
			'customerBranchId': new FormControl(''),
			'exportformat': new FormControl(''),
			'reporttype': new FormControl(''),
		});
		
		const headers = new HttpHeaders({'Content-Type':'application/json'});
	  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branchs = data['populateBranch'];
			  															}); 
	}
	 get customerBranchId(){ return this.customerLandingForm.get('customerBranchId')};
	 get exportformat(){ return this.customerLandingForm.get('exportformat')};
	 get reporttype(){ return this.customerLandingForm.get('reporttype')};
	  
		  
    ngAfterViewInit() {
		   $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
    }
	  
	  getTodaysDate(){
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/currentdate',
							 			JSON.stringify({ requestType : 'shortDate' }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.customer.startDate= data['date'];
				  														this.customer.endDate= data['date'];
			  															});
	 }
	 
	 
//	 checkAll(event){
//	 this.sizes.forEach(x=>x.state=event.target.checked)
//	 }
//	 
//	 isAllChecked(){
//		 return this.sizes.every(_=>_.state);
//	 }
	 
	 
	  onSubmit() {
		  this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.customer[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
				
				if(this.bootstrapFormValues[i].name=="CustomerStartDate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Start Date!<br>';
						this.customerstartdatemsg="<font class='select2ErrorMsg'>Enter Start Date</font>";
						
					}else{
						this.bootstrapMsg='';
						this.customerstartdatemsg = "";
					}
				}
				
				if(this.bootstrapFormValues[i].name=="CustomerEndDate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter End Date!<br>';
						this.customerenddatemsg="<font class='select2ErrorMsg'>Enter End Date</font>";
						
					}else{
						this.bootstrapMsg='';
						this.customerenddatemsg = "";
					}
				}
			}//end of for loop
			
		  if(this.bootstrapMsg != ''){
				this.msg[0]='Error';
			 	this.msg[1]='Enter the Mandatory Fields' ;
				this.toast.showtoast(this.msg);
//				this.bootstrapMsg='';
			}
		  console.log(this.customerLandingForm.invalid);
		   console.log(this.bootstrapMsg);
		  this.customer['requestType']='landing';
		   if(!this.customerLandingForm.invalid && this.bootstrapMsg == ''){
			  
		   		const headers = new HttpHeaders({'Content-Type':'application/json'});
	  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer',JSON.stringify(this.customer),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.customerSummary = data['customerSummary'];
				  														this.grandTotal = data['total'];
		    if(this.customerSummary .length!=0){
			    this.listHide = false;
		  }
			  															});									
																	}
		
		  console.log(this.customer);
		  
	  }
}

export class customerData { 
	startDate: string='';
	endDate: string='';
	customerBranchId: string[];
	exportformat: string='';
	reporttype: string='';
	selectAllId: string;
}



